<template>
  <div class="baseTitleInfo">
    <div class="title">{{ title }}</div>
    <div
    class="remark"
    v-for="(item, index) of remark"
    :key="index">
    {{ item }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'BaseTitleInfo',
  props: {
    title: { // 标题
      type: String,
      required: true
    },
    remark: { // 备注
      type: Array,
      required: true
    }
  }
}
</script>

<style>
/* 标题版块样式 */
.baseTitleInfo .title {
  height: 20px;
  line-height: 20px;
  font-size: 14px;
}
/* 标题备注样式 */
.baseTitleInfo .remark {
  height: 18px;
  line-height: 18px;
}
/* 设置文字样式 */
.baseTitleInfo .title,
.baseTitleInfo .remark {
  text-align: center;
}
</style>



